<template>
  <div class="financial-report-container">
    <div class="report-header">
      <h2>会计报表</h2>
      <div class="report-controls">
        <el-radio-group v-model="activeReport" @change="handleReportChange">
          <el-radio-button label="balanceSheet">资产负债表</el-radio-button>
          <el-radio-button label="incomeStatement">利润表</el-radio-button>
          <el-radio-button label="cashFlowStatement">现金流量表</el-radio-button>
        </el-radio-group>
        
        <div class="period-control">
          <el-select v-model="selectedPeriod" placeholder="选择报表期间" @change="handlePeriodChange">
            <el-option
              v-for="item in periodOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button type="primary" @click="exportReport">导出报表</el-button>
        </div>
      </div>
    </div>
    
    <!-- 报表加载状态 -->
    <el-loading v-if="loading" fullscreen text="正在加载报表数据..."></el-loading>
    
    <!-- 资产负债表 -->
    <div v-if="activeReport === 'balanceSheet' && balanceSheetData" class="report-content">
      <div class="report-title">
        <h3>资产负债表</h3>
        <span class="report-date">{{ formatPeriod(selectedPeriod) }}</span>
      </div>
      <div class="balance-sheet-layout">
        <!-- 资产部分 -->
        <div class="balance-sheet-section">
          <h4>资产</h4>
          <el-table :data="getAssetsData()" stripe>
            <el-table-column prop="name" label="项目" width="300" show-overflow-tooltip></el-table-column>
            <el-table-column prop="amount" label="金额" width="200">
              <template slot-scope="scope">
                {{ formatCurrency(scope.row.amount) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
        
        <!-- 负债和所有者权益部分 -->
        <div class="balance-sheet-section">
          <h4>负债和所有者权益</h4>
          <el-table :data="getLiabilitiesAndEquityData()" stripe>
            <el-table-column prop="name" label="项目" width="300" show-overflow-tooltip></el-table-column>
            <el-table-column prop="amount" label="金额" width="200">
              <template slot-scope="scope">
                {{ formatCurrency(scope.row.amount) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    
    <!-- 利润表 -->
    <div v-if="activeReport === 'incomeStatement' && incomeStatementData" class="report-content">
      <div class="report-title">
        <h3>利润表</h3>
        <span class="report-date">{{ formatPeriod(selectedPeriod) }}</span>
      </div>
      <el-table :data="getIncomeStatementData()" stripe>
        <el-table-column prop="name" label="项目" width="300" show-overflow-tooltip></el-table-column>
        <el-table-column prop="amount" label="金额" width="200">
          <template slot-scope="scope">
            {{ formatCurrency(scope.row.amount) }}
          </template>
        </el-table-column>
      </el-table>
    </div>
    
    <!-- 现金流量表 -->
    <div v-if="activeReport === 'cashFlowStatement' && cashFlowStatementData" class="report-content">
      <div class="report-title">
        <h3>现金流量表</h3>
        <span class="report-date">{{ formatPeriod(selectedPeriod) }}</span>
      </div>
      <div class="cash-flow-layout">
        <!-- 经营活动 -->
        <div class="cash-flow-section">
          <h4>经营活动产生的现金流量</h4>
          <el-table :data="cashFlowStatementData.operatingActivities" stripe>
            <el-table-column prop="name" label="项目" width="350" show-overflow-tooltip></el-table-column>
            <el-table-column prop="amount" label="金额" width="200">
              <template slot-scope="scope">
                {{ formatCurrency(scope.row.amount) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
        
        <!-- 投资活动 -->
        <div class="cash-flow-section">
          <h4>投资活动产生的现金流量</h4>
          <el-table :data="cashFlowStatementData.investingActivities" stripe>
            <el-table-column prop="name" label="项目" width="350" show-overflow-tooltip></el-table-column>
            <el-table-column prop="amount" label="金额" width="200">
              <template slot-scope="scope">
                {{ formatCurrency(scope.row.amount) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
        
        <!-- 筹资活动 -->
        <div class="cash-flow-section">
          <h4>筹资活动产生的现金流量</h4>
          <el-table :data="cashFlowStatementData.financingActivities" stripe>
            <el-table-column prop="name" label="项目" width="350" show-overflow-tooltip></el-table-column>
            <el-table-column prop="amount" label="金额" width="200">
              <template slot-scope="scope">
                {{ formatCurrency(scope.row.amount) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
        
        <!-- 现金及现金等价物净增加额 -->
        <div class="cash-flow-section">
          <h4>现金及现金等价物净增加额</h4>
          <el-table :data="getCashFlowSummaryData()" stripe>
            <el-table-column prop="name" label="项目" width="350" show-overflow-tooltip></el-table-column>
            <el-table-column prop="amount" label="金额" width="200">
              <template slot-scope="scope">
                {{ formatCurrency(scope.row.amount) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 导入财务报表API
import { financialReportAPI } from '@/api/api';

export default {
  name: 'FinancialReport',
  data() {
    return {
      // 当前激活的报表类型
      activeReport: 'balanceSheet',
      // 选中的报表期间
      selectedPeriod: '2025-10',
      // 报表期间选项
      periodOptions: [],
      // 加载状态
      loading: false,
      // 资产负债表数据
      balanceSheetData: null,
      // 利润表数据
      incomeStatementData: null,
      // 现金流量表数据
      cashFlowStatementData: null,
      // 使用模拟数据标志
      useMockData: false // 默认为false，使用实际API数据
    };
  },
  created() {
    // 初始化数据
    this.loadPeriodOptions();
  },
  mounted() {
    // 加载默认报表数据
    this.loadReportData();
  },
  methods: {
    // 处理报表类型切换
    handleReportChange() {
      this.loadReportData();
    },
    // 处理报表期间切换
    handlePeriodChange() {
      this.loadReportData();
    },
    // 加载报表期间选项
    loadPeriodOptions() {
      // 生成最近12个月的选项，因为后端可能没有提供获取期间的接口
      const periodOptions = [];
      const now = new Date();
      
      for (let i = 0; i < 12; i++) {
        const date = new Date(now.getFullYear(), now.getMonth() - i, 1);
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const value = `${year}-${month}`;
        const label = `${year}年${month}月`;
        
        periodOptions.push({ value, label });
      }
      
      this.periodOptions = periodOptions;
      
      // 如果需要调用实际API，可以取消下面的注释
      /*
      if (this.useMockData) {
        // 使用模拟数据
        const mockResponse = financialReportMock['GET /financial-report/periods'].handler({}, { json: (data) => data });
        this.periodOptions = mockResponse.data;
      } else {
        // 实际API调用
        financialReportAPI.getPeriods()
          .then(response => {
            if (response.code === 200) {
              this.periodOptions = response.data;
            } else {
              this.$message.error(response.message);
            }
          })
          .catch(error => {
            console.error('加载报表期间失败:', error);
            this.$message.error('加载报表期间失败');
          });
      }
      */
    },
    // 加载报表数据
    loadReportData() {
      this.loading = true;
      
      if (this.useMockData) {
        // 模拟网络延迟
        setTimeout(() => {
          this.loadMockData();
          this.loading = false;
        }, 500);
      } else {
        // 实际API调用
        this.loadApiData();
      }
    },
    // 加载模拟数据（已注释，因为我们使用实际API）
    loadMockData() {
      // 由于useMockData已设置为false，此方法不会被调用
      // 保留方法结构以确保代码完整性
      console.log('模拟数据加载功能已禁用');
    },
    // 加载API数据
    loadApiData() {
      // 解析日期格式
      let date;
      let startDate, endDate;
      
      // 处理不同类型报表的日期参数
      if (this.activeReport === 'balanceSheet') {
        // 资产负债表只需要一个日期
        date = this.selectedPeriod;
      } else {
        // 利润表和现金流量表需要开始和结束日期
        // 简单处理：如果是月度，开始日期为月初，结束日期为月末
        if (this.selectedPeriod.includes('-') && this.selectedPeriod.split('-').length === 2) {
          const [year, month] = this.selectedPeriod.split('-');
          startDate = `${year}-${month}-01`;
          // 计算月末日期
          const lastDay = new Date(year, month, 0).getDate();
          endDate = `${year}-${month}-${lastDay}`;
        } else {
          // 默认为当月
          const now = new Date();
          const year = now.getFullYear();
          const month = String(now.getMonth() + 1).padStart(2, '0');
          startDate = `${year}-${month}-01`;
          const lastDay = new Date(year, now.getMonth() + 1, 0).getDate();
          endDate = `${year}-${month}-${lastDay}`;
        }
      }
      
      let apiPromise;
      
      switch (this.activeReport) {
        case 'balanceSheet':
          apiPromise = financialReportAPI.getBalanceSheet(date);
          break;
        case 'incomeStatement':
          apiPromise = financialReportAPI.getIncomeStatement(startDate, endDate);
          break;
        case 'cashFlowStatement':
          apiPromise = financialReportAPI.getCashFlowStatement(startDate, endDate);
          break;
      }
      
      if (apiPromise) {
        apiPromise
          .then(response => {
            this.loading = false;
            if (response.code === 200) {
              switch (this.activeReport) {
                case 'balanceSheet':
                  this.balanceSheetData = response.data;
                  break;
                case 'incomeStatement':
                  this.incomeStatementData = response.data;
                  break;
                case 'cashFlowStatement':
                  this.cashFlowStatementData = response.data;
                  break;
              }
            } else {
              this.$message.error(response.message);
            }
          })
          .catch(error => {
            this.loading = false;
            console.error('加载报表数据失败:', error);
            this.$message.error('加载报表数据失败');
          });
      }
    },
    // 导出报表
    exportReport() {
      this.$message.info('报表导出中...');
      
      // 实际API调用
      const params = {
        reportType: this.getReportTypeLabel(),
        period: this.selectedPeriod
      };
      
      financialReportAPI.exportReport(params)
        .then(response => {
          // 处理文件下载
          const blob = new Blob([response.data]);
          const url = window.URL.createObjectURL(blob);
          const link = document.createElement('a');
          link.href = url;
          link.download = `${this.getReportTypeLabel()}_${this.selectedPeriod}.xlsx`;
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          window.URL.revokeObjectURL(url);
          this.$message.success('报表导出成功');
        })
        .catch(error => {
          console.error('导出报表失败:', error);
          this.$message.error('导出报表失败');
        });
    },
    // 获取报表类型标签
    getReportTypeLabel() {
      switch (this.activeReport) {
        case 'balanceSheet': return '资产负债表';
        case 'incomeStatement': return '利润表';
        case 'cashFlowStatement': return '现金流量表';
        default: return '财务报表';
      }
    },
    // 格式化报表期间
    formatPeriod(period) {
      if (period.includes('-')) {
        const parts = period.split('-');
        if (parts.length === 2) {
          return `${parts[0]}年${parts[1]}月`;
        } else if (parts.length === 3) {
          return `${parts[0]}年${parts[1]}-${parts[2]}月`;
        }
      }
      return period;
    },
    // 格式化货币
    formatCurrency(value) {
      if (typeof value !== 'number') return '0.00';
      return value.toLocaleString('zh-CN', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      });
    },
    // 获取资产数据
    getAssetsData() {
      if (!this.balanceSheetData) return [];
      
      const assetsData = [];
      // 添加流动资产
      assetsData.push(...this.balanceSheetData.assets.流动资产);
      assetsData.push(this.balanceSheetData.assets.流动资产合计);
      // 添加非流动资产
      assetsData.push(...this.balanceSheetData.assets.非流动资产);
      assetsData.push(this.balanceSheetData.assets.非流动资产合计);
      // 添加资产总计
      assetsData.push(this.balanceSheetData.assets.资产总计);
      
      return assetsData;
    },
    // 获取负债和所有者权益数据
    getLiabilitiesAndEquityData() {
      if (!this.balanceSheetData) return [];
      
      const liabilitiesAndEquityData = [];
      // 添加流动负债
      liabilitiesAndEquityData.push(...this.balanceSheetData.liabilities.流动负债);
      liabilitiesAndEquityData.push(this.balanceSheetData.liabilities.流动负债合计);
      // 添加非流动负债
      liabilitiesAndEquityData.push(...this.balanceSheetData.liabilities.非流动负债);
      liabilitiesAndEquityData.push(this.balanceSheetData.liabilities.非流动负债合计);
      // 添加负债合计
      liabilitiesAndEquityData.push(this.balanceSheetData.liabilities.负债合计);
      // 添加所有者权益
      liabilitiesAndEquityData.push(...this.balanceSheetData.equity);
      
      return liabilitiesAndEquityData;
    },
    // 获取利润表数据
    getIncomeStatementData() {
      if (!this.incomeStatementData) return [];
      
      const incomeStatementData = [];
      // 添加营业收入
      incomeStatementData.push(...this.incomeStatementData.operatingRevenue);
      // 添加营业成本
      incomeStatementData.push(...this.incomeStatementData.operatingCost);
      // 添加营业利润
      incomeStatementData.push(this.incomeStatementData.operatingProfit);
      // 添加营业外收入
      incomeStatementData.push(...this.incomeStatementData.nonOperatingRevenue);
      // 添加营业外支出
      incomeStatementData.push(...this.incomeStatementData.nonOperatingCost);
      // 添加营业外收支净额
      incomeStatementData.push(this.incomeStatementData.nonOperatingProfit);
      // 添加利润总额
      incomeStatementData.push(this.incomeStatementData.totalProfit);
      // 添加所得税
      incomeStatementData.push(...this.incomeStatementData.tax);
      // 添加净利润
      incomeStatementData.push(this.incomeStatementData.netProfit);
      
      return incomeStatementData;
    },
    // 获取现金流量表汇总数据
    getCashFlowSummaryData() {
      if (!this.cashFlowStatementData) return [];
      
      const summaryData = [];
      // 添加净增加额
      summaryData.push(this.cashFlowStatementData.netIncreaseInCash);
      // 添加期初余额
      summaryData.push(this.cashFlowStatementData.beginningCashBalance);
      // 添加期末余额
      summaryData.push(this.cashFlowStatementData.endingCashBalance);
      
      return summaryData;
    }
  }
};
</script>

